































































































































import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Profile extends Vue {
  private showSkills: boolean = false;
  private VueJS: any = 70;
  private VueTS: any = 60;
  private HTML: any = 80;
  private CSS: any = 80;
  private PHP: any = 50;
  private MYSQL: any = 55;
  private actionList: any = [
    "Profile",
    "Skills",
    "Experience"
    // "Education",
    // "Contact"
  ];
  private showExp: boolean = false;
  private showProfile: boolean = false;
  private pStyle: any = {
      fontSize: '16px',
      color: 'rgba(0,0,0,0.85)',
      lineHeight: '24px',
      display: 'block',
      marginBottom: '16px'
  }
  private activeNames: any = '';
  private expList: any = [
    {
      title: 'Front-end Developer (October 2020 - April 2021)',
      description: `Develop and build mobile apps, templates, re-usable 
        codes and components using Vue JS framework at AYX TECHNOLOGY INC.
      `,
    },
    {
      title: 'Junior Front-End Developer (July 2019 - August 2020)',
      description: `Develop and build client websites at Digispark Tech Corp.
      Assigned to maintain and debug issues for existing client websites. Used customized theme and also 
      customized plugin if needed. Created client websites 
      for businesses. Also, maintain client's websites.`,
    },
  ];

  private showDetails(index: any) {
    if (index == 0) {
      this.showProfile = true;
    } else if (index == 1) {
      this.showSkills = true;
    } else {
      this.showExp = true;
    }
  }

}
