




















import { Component, Vue } from 'vue-property-decorator';
import Header from '@/components/common/Header.vue'; // @ is an alias to /src
import Profile from '@/components/common/Profile.vue'; // @ is an alias to /src
import Content from '@/components/common/Content.vue'; // @ is an alias to /src

@Component({
  components: {
    Header,
    Profile,
    Content
  },
})
export default class Home extends Vue {
  private isLoading: boolean = true;

  private mounted() {
    setTimeout(() => {
        this.isLoading = false;
    }, 3000);
  }
}
