






































import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Header extends Vue {
  private rateValue: any = 0;
  private publicPath: any = process.env.BASE_URL;
  private showRate: boolean = false;
  private showAbout: boolean = false;
  private rate_loading: boolean = false;
  private headerOptions: any = [
    { title: 'Mobile', icon: 'md-phone-portrait', url: 'https://iamkean.github.io/wap.portfolio/#/' },
    { title: 'GitHub', icon: 'logo-github', url: 'https://github.com/iAmKean' },
    { title: 'Download CV', icon: 'md-download', url: '' },
    { title: 'Rate', icon: 'md-thumbs-up', url: '' },
    { title: 'About', icon: 'md-information-circle', url: '' },
  ];

  private selectMenu(index: any) {
    if (index == 0) {
      window.location.href = this.headerOptions[index].url
    } else if (index == 1) {
      window.open(this.headerOptions[index].url);
    } else if (index == 2) {

    } else if (index == 3) {
      this.showRate = true;
    } else {
      this.showAbout = true;
    }
  }

  private submit() {
    this.rate_loading = true;
    setTimeout(() => {
        this.rate_loading = false;
        this.showRate = false;
        this.rateValue = 0;
        this.$Message.success('Rate success');
    }, 2000);
  }
}
